import { ExclamationTriangleIcon, CheckCircleIcon, InformationCircleIcon, XMarkIcon } from '@heroicons/react/20/solid'
import { Fragment, useState } from 'react'
import { Transition } from '@headlessui/react'
import { useDispatch } from 'react-redux'
import { HIDE_UPDATE_MESSAGE } from '../constants/userConstants'

export function Error({ children }) {
    return (
        <div className="rounded-md bg-red-100 p-4 mt-8 shadow-xl">
            <div className="flex">
                <div className="flex-shrink-0">
                    <ExclamationTriangleIcon className="h-5 w-5 text-red-500" aria-hidden="true" />
                </div>

                <div className="ml-3">
                    <h3 className="text-sm font-medium text-red-800">Attention needed</h3>
                    <div className="mt-2 text-sm text-red-700">
                        <p>
                            { children }
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export function Success({ children }) {
    return (
        <div className="rounded-md bg-green-100 p-4 mt-8 shadow-xl">
            <div className="flex">
                <div className="flex-shrink-0">
                    <CheckCircleIcon className="h-5 w-5 text-green-500" aria-hidden="true" />
                </div>

                <div className="ml-3">
                    <h3 className="text-sm font-medium text-green-800">Success</h3>
                    <div className="mt-2 text-sm text-green-700">
                        <p>
                            { children }
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export function Info({ children }) {
    return (
        <div className="rounded-md bg-blue-100 p-4 mt-8 shadow-xl">
            <div className="flex">
                <div className="flex-shrink-0">
                    <InformationCircleIcon className="h-5 w-5 text-blue-500" aria-hidden="true" />
                </div>

                <div className="ml-3">
                    <h3 className="text-sm font-medium text-blue-800">{ children }</h3>
                </div>
            </div>
        </div>
    )
}
  
export function SuccessNotification({children, ...props}) {
    const [show, setShow] = useState(true)
    const dispatch = useDispatch()

    setTimeout(() => {
        dispatch({ type: HIDE_UPDATE_MESSAGE })
    }, 5000)

  return (
    <>
      {/* Global notification live region, render this permanently at the end of the document */}
      <div
        aria-live="off"
        className="pointer-events-none fixed inset-0 flex items-end px-4 py-6 sm:items-start sm:p-6"
      >
        <div className="flex w-full flex-col items-center space-y-4 sm:items-end">
          {/* Notification panel, dynamically insert this into the live region when it needs to be displayed */}
          <Transition
            show={show}
            as={Fragment}
            enter="transform ease-out duration-300 transition"
            enterFrom="translate-y-2 opacity-0 sm:translate-y-0 sm:translate-x-2"
            enterTo="translate-y-0 opacity-100 sm:translate-x-0"
            leave="transition ease-in duration-100"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="pointer-events-auto w-full max-w-sm overflow-hidden rounded-lg bg-white shadow-lg ring-1 ring-black ring-opacity-5">
              <div className="p-4">
                <div className="flex items-start">
                  <div className="flex-shrink-0">
                    <CheckCircleIcon className="h-6 w-6 text-green-400" aria-hidden="true" />
                  </div>
                  <div className="ml-3 w-0 flex-1 pt-0.5">
                    <p className="text-sm font-medium text-gray-900">Success!</p>
                    <p className="mt-1 text-sm text-gray-500">{children}</p>
                  </div>
                  <div className="ml-4 flex flex-shrink-0">
                    <button
                      type="button"
                      className="inline-flex rounded-md bg-white text-gray-400 hover:text-gray-500 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                      onClick={() => {
                        setShow(false)
                      }}
                    >
                      <span className="sr-only">Close</span>
                      <XMarkIcon className="h-5 w-5" aria-hidden="true" />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </Transition>
        </div>
      </div>
    </>
  )
}
